@font-face {
	font-family: 'good-glyphs';
	src: url('GoodGlyphs-No1.ttf');
  }

body, html {
	background-color: #bdb2ff;
}
.home {
	padding: 2rem 2rem;
	min-height: 100%;
	width: 100%;
	box-sizing: border-box;
	overflow: hidden;
	flex-direction: column;
}

.ico {
	font-family:'good-glyphs';
	float: right;
	position: fixed;
    bottom: 0;
    right: 0;
	font-size: 6rem;
	padding: 1rem 1rem;
	cursor: pointer;
}
.nc-image {
	border-radius:10px;
	width: 10rem;
}

.name_card {
	padding: 1rem 1rem;
	font-size: 1.5rem;
	font-weight: bold;
	margin-bottom:1.5rem;
	display: flex;
	justify-content: space-between;

	background: linear-gradient(90deg, black 50%, transparent 50%), linear-gradient(90deg, black 50%, transparent 50%), linear-gradient(0deg, black 50%, transparent 50%), linear-gradient(0deg, black 50%, transparent 50%);
	background-repeat: repeat-x, repeat-x, repeat-y, repeat-y;
	background-size: 15px 4px, 15px 4px, 4px 15px, 4px 15px;
	animation: border-dance 20s infinite linear;
}

@keyframes border-dance {
	0% {
	  background-position: 0 0, 100% 100%, 0 100%, 100% 0;
	}
	100% {
	  background-position: 100% 0, 0 100%, 0 0, 100% 100%;
	}
  }

.samples_header {
	font-size: 1.35rem;
	font-style: italic;
}
.samples {
	padding-top: 1rem;
	display: flex;
	flex-direction: column;
	width: 560px;
	margin: 0 auto;
}

.sample_item_title {
	font-size: 1.15rem;
	padding-bottom: .75rem;
	font-weight: bold;
}

.sample_item {
	padding: 2rem 0;
    display: flex;
    flex-direction: column;
}

.sample_item_link {
	margin-top: 1rem;
	color: black;
	display: block;
	font-weight: bold;
	text-decoration: none;
}
.sample_item_link:hover {
	color: limegreen;
	text-decoration: underline;
}

.sav {
	display: block;
	font-size: 2rem;
	font-weight: bolder;
	margin-bottom: .75rem;
}

.sav_email {
	text-decoration: none;
	color: #444;
	font-size: 1.35rem;
}

.sav_email:hover {
	text-decoration: underline;
}

.sample_item_media img {
	width:560px;
}


.sample_item_role {
	font-size: 1.15rem;
	padding-top: .75rem;
}

.bio {
	font-size: 1.15rem;
	line-height: 1.75rem;
	max-width: 75%;
	min-width: 45rem;
	text-align: justify;
}

.links {
	display: flex;
	margin-top: 3rem;
	flex-wrap: wrap;
}

.link {
	/* background-color: black; */
	border: 2px solid black;
	color: black;
	padding: 20px;
	margin-right:1rem;
	margin-bottom:1rem;
	text-decoration:none;
	font-weight: bold;
	width: 120px;
	text-align: center;
}
.link:hover {
	color: white;

	background-color: black;
}
hr {
  width:300%;
  margin-left:-100%;
}

@media only screen and (max-width: 768px) {
	.sample_item_media iframe {
		width:100%;
	}

	.nc-image{
		width: 8rem;
		height: 8rem;
	}
	.bio {
		max-width: 100%;
		min-width: auto;
	}

	.sample_item_media img {
		width:100%;
	}
	.samples {
		width: auto;
	}
	/* .ico {
		position: relative;
		margin: 0 auto;
	} */
}

@media only screen and (max-width: 400px) {

	.ico {
		display: none;
	}
}
